import React from 'react';
import firebase from 'firebase/app';
import 'firebase/auth';
import { Button, Card, CardBody, CardGroup, Col, Container, Form, FormGroup , Input, InputGroup, InputGroupAddon,InputGroupText, Row } from 'reactstrap';
import {Icon} from 'react-icons-kit';
import {at} from 'react-icons-kit/fa/at'
import {pencil} from 'react-icons-kit/fa/pencil'
import { firebaseApp } from '../base';

class Login extends React.Component{

    state={
        uid : null,
        email:'',
        password:''
    }

    componentDidMount(){
        firebase.auth().onAuthStateChanged(user=>{
            if(user){
                return this.authHandler({user});
            }
        })
    }

    authHandler = async authData => {
        this.setState({
            uid : authData.user.uid,
        })
        return this.redirect();
    }

    redirect () {
        this.props.history.replace('/');
    }

    logout = async() => {
        await firebase.auth().signOut()
        this.setState({uid:null})
    }

    authenticate = (provider) => {
        const authProvider = new firebase.auth[`${provider}AuthProvider`]();
        firebaseApp.auth().signInWithPopup(authProvider).then(this.authHandler);
    };

    handleUserInput = (e) => {
        let name = e.target.name;
        let value = e.target.value;
        this.setState({[name]:value});
    }

    handleClickEmail = () => {
        firebase.auth().signInWithEmailAndPassword(this.state.email, this.state.password)
        .then(res => this.redirect());
    }
    
    render(){
        return(
            <div className="app flex-row align-items-center">
                <Container>
                <Row className="justify-content-center">
                    <Col md="9">
                    <CardGroup>
                        <Card className="p-4">
                        <CardBody>
                            <Form>
                            <h1>Login</h1>
                            <p className="text-muted">Sign In to your account </p>
                            <FormGroup className = "form-group">
                                <InputGroup className="mb-4">
                                <InputGroupAddon addonType="prepend">
                                    <InputGroupText>
                                    <Icon className="icon" size={20} icon={at}/>
                                    </InputGroupText>
                                </InputGroupAddon>
                                <Input 
                                    name='email' 
                                    onChange={(events) => {this.handleUserInput(events)}} 
                                    type="text" 
                                    placeholder="Email" 
                                    autoComplete="Email"
                                />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <InputGroup className="mb-4">
                                <InputGroupAddon addonType="prepend">
                                    <InputGroupText>
                                        <Icon className="icon" size={20} icon={pencil}/>
                                    </InputGroupText>
                                </InputGroupAddon>      
                                <Input
                                    name='password' 
                                    onChange={(events) => {this.handleUserInput(events)}} 
                                    type="password" 
                                    placeholder="Password" 
                                    autoComplete="current-password"
                                />
                                </InputGroup>
                            </FormGroup>
                            <Row>
                                <Col xs="6">
                                <Button 
                                    onClick={this.handleClickEmail} 
                                    color="primary" 
                                    className="btn btn-primary">Login
                                </Button>
                                </Col>
                            </Row>
                            </Form>
                        </CardBody>
                        </Card>
                        <Card className="text-white bg-white py-5 d-md-down-none" style={{ width: 100 + '%'}}>
                        <CardBody className="text-center">
                            <img src="https://www.royalspijkenisse.nl/wp-content/uploads/2017/10/logo-royal-web-v3.png" alt="royal-logo"/>
                        </CardBody>
                        </Card>
                    </CardGroup>
                    </Col>
                </Row>
                </Container>
            </div>
        )
    }
}

export default Login;